







































var userLoginInfo;
export default {
	data() {
		return {
			addressList: [],
			key: ''
		}
	},
	onShow() {
		userLoginInfo = this.userLoginInfo('../addAdress/address_management', 1);
		// console.log(userLoginInfo.id);
		this.addressKee();
	},
	onLoad: function(params) {
	},
	methods: {
		navigateBack: function() {
			uni.navigateBack();
		},
		// 用户自己选择收货地址
		DeliveryAddress: function(addressData) {
			uni.setStorageSync('selectAddress',addressData);
			uni.navigateBack({
				delta:1
			})
		},
		// 用户已保存的地址信息
		addressKee() {
			uni.request({
				url: this.apiUrl + 'user/getAddress',
				method: 'GET',
				data: {
					// 判断用户是否登录
					uid: userLoginInfo.id
				},
				success: (res) => {
					if (res.data.code == 0) {
						
						this.addressList = res.data.data;
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		},
		// 添加收获地址
		addAdress: function() {
			uni.navigateTo({
				url: './adress'
			})
		},
		// 编辑
		edit: function(addressId) {
			uni.navigateTo({
				url: './adress?editId=' + addressId
			})
		},
		// 删除收获地址
		delect: function(delectId) {
			uni.showModal({
				// title: '提示',
				content: '确定要删除该收获地址吗？',
				success: (res) => {
					if (res.confirm) {
						this.delAddress(delectId);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		delAddress: function(address_id) {
			// 执行删除操作
			uni.request({
				url: this.apiUrl + 'user/delAddress',
				method: 'GET',
				data: {
					// 判断用户是否登录
					uid: userLoginInfo.id,
					id: address_id
				},
				success: (res) => {
					if (res.data.code == 0) {
						// console.log(res.data.data);
						this.addressList = [],
						this.addressKee();
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		}
	}
}
